/*star shadow*/
.shadow{
  position:absolute;
  padding-top: 0px;
  border-radius: 10%;
  width: 77px;
  height: 77px;
  border-radius: 100%;
  z-index:100;
}
.shadow:before{
  content:'';
  position:absolute;
  top:0;
  left:0;
  border-radius: 5%;
  background: linear-gradient(45deg, #0019fb, #ff00f2, #ff0000, #fffb00, #1100fb, #ff0000, #f700ff, #0019fb);
  background-size:6000%;
  width: calc(100% );
  height: calc(100% );
  z-index:0;
  -webkit-animation:animate 100s linear infinite;
          animation:animate 100s linear infinite;
  -webkit-filter: blur(8px);
          filter: blur(8px);
}


@-webkit-keyframes animate{
      0%{
          background-position: 0 0;;
      }
      50%{
          background-position: 400% 0;
      }
      100%{
          background-position: 0 0;
      }
}


@keyframes animate{
      0%{
